@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  /* box-sizing: border-box;
  margin: 0;
  padding: 0; */
  font-family: "Montserrat", sans-serif;
  background-color: #2d2e2d;
  color: #f1f5c4;
}

.navbar-logo {
  justify-self: start;
  text-decoration: none;
  font-size: 3rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
  font-size: 2rem;
}

.coin-app {
  margin: 25px 0;
  color: #f1f5c4;
  /* new */
  position: relative;
  min-height: 95vh;
}

.topnav {
  overflow: hidden;
  padding-bottom: 2%;
}

.topnav .navbar-logo {
  float: left;
  display: block;
}
.searchTerm {
  border: 5px solid #3fa287;
  outline: none;
  height: 2rem;
  /* margin-right: 5%; */
  padding-left: 4%;
  border-radius: 5px;
  /* width: 100%; */
}
.search-container {
  display: flex;
  margin-right: 1%;
  float: right;
  /* width: 15%; */
}

.searchTerm:focus {
  color: #f1f5c4;
}

.footer {
  text-align: center;
  width: 100%;
  padding-top: 2vh;
}
.git-logo {
  padding-left: 2%;
}
.no-result {
  font-size: 2.5rem;
  text-align: center;
  padding-top: 2rem;
  animation: mymove 5s infinite;
}
.error-logo {
  font-size: 2rem;
  padding-right: 1%;
}

.error-and-logo {
  animation: make-visible 0s 1.1s forwards;
  visibility: hidden;
}
@keyframes make-visible {
  to {
    visibility: visible;
  }
}
.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #f1f5c4;
  border-color: #f1f5c4 transparent #f1f5c4 transparent;
  animation: lds-dual-ring 1s forwards;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  to {
    visibility: hidden;
  }
}

@media screen and (max-width: 950px) {
  .navbar-logo {
    font-size: 2.5rem;
    transition: 300ms linear;
  }
  .navbar-icon {
    font-size: 1.5rem;
  }

  .search-container {
    width: 100%;
    display: inline-block;
    margin-right: 0;
    padding-top: 2.5%;
  }
  .searchTerm {
    width: 93%;
  }
}
@media screen and (max-width: 700px) {
  .searchTerm {
    width: 92%;
  }
  .error-and-logo {
    font-size: 2rem;
  }
  .error-logo {
    font-size: 1.4rem;
  }
}

@media screen and (max-width: 600px) {
  .searchTerm {
    width: 91%;
  }
}

.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;

  align-items: center;
  /* height: 80px; */
  height: 4em;
  border-bottom: 3px solid #3fa287;
  /* border-radius: 4%; */
  width: 85%;
}

.coin {
  display: flex;
  align-items: center;
  /* padding-right: 14px; */
  width: 50%;
}

.coin h1 {
  font-size: 16px;
  width: 220px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 2%;
}

.coin-symbol {
  text-transform: uppercase;
  padding-left: 5%;
}

.coin-data {
  display: flex;
  /* text-align: right; */
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 100px;
  text-align: right;
}

.coin-volume {
  width: 175px;
  text-align: right;
}

.coin-marketcap {
  width: 175px;
  text-align: right;
}

.coin-percent {
  width: 75px;
  text-align: right;
}

.red {
  color: #c14939;
}

.green {
  color: #4dd146;
}

.coin-volume-small,
.coin-marketcap-small {
  display: none;
}

@media screen and (max-width: 950px) {
  .coin-row {
    display: block;
    height: 7em;
    border-bottom: 5px solid #3fa287;
    width: 96% !important;
  }

  .coin {
    /* margin-top: 2%; */
    width: 100% !important;
  }
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 2%;
  }
  .coin h1 {
    width: 100%;
    font-size: 24px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .coin-symbol {
    margin-top: 0;
  }

  .coin-price {
    width: 80px;
  }
  .coin-volume {
    width: 125px;
  }
  .coin-percent {
    width: 30px;
  }
  .coin-marketcap {
    width: 130px;
    display: none;
  }
  .coin-volume {
    display: none;
  }
  .coin-volume-small,
  .coin-marketcap-small {
    display: inline-block;
  }
}

@media screen and (max-width: 1280px) {
  .coin {
    width: 65%;
  }
  .coin-row {
    width: 95%;
  }
}

@media screen and (max-width: 350px) {
  .coin h1 {
    font-size: 16px;
  }
  .coin img {
    height: 28px;
    width: 28px;
  }
  .coin {
    margin-top: 2%;
    /* width: 100% !important; */
  }
  .coin-row {
    /* width: 100% !important; */
    height: 7em;

    /* overflow-y: auto; */
  }
  .coin-volume {
    width: 120px;
  }
}

